.box {
	background-color: #61dafb;
	height: 100vh;
	width: 0;
	position: absolute;
	z-index: 10;
	top: 0;
	right: 0;
	transition: 0.7s all ease-out;
	background-position: center center;
	background-size: cover;
	overflow: hidden;
}

.show {
	width: 100vw;
}

.exit {
	position: absolute;
	top: 10px;
	left: 30px;
	font-size: 70px;
	font-weight: 700;
	transition: 0.6s all ease-out;
	color: #f00000;
	cursor: pointer;
	text-align: center;

	&:hover {
		transform: rotate(180deg);
	}
}

.hidden {
	display: none;
}

.error {
	font-size: 100px;
	color: #ff1f4e;
	align-items: center;
	display: flex;
	justify-content: center;
	margin-top: 5%;
	text-transform: uppercase;
	font-weight: 700;
	text-shadow: 8px 10px 5px whitesmoke;
}

.boxItems {
	border: 5px solid #d2d2d2;
	display: flex;
	flex-direction: column;
	max-width: 450px;
	height: 450px;
	width: 100%;
	margin: 10% auto;
	padding: 20px;
	border-radius: 35px;
	background-color: RGBA(7, 0, 0, 0.09);
	align-items: center;
	justify-content: space-evenly;
}

.city {
	font-size: 52px;
	font-weight: 800;
	letter-spacing: 2px;
	color: whitesmoke;
	padding: 0;
	margin: 0;
}

.temp {
	font-size: 100px;
	font-weight: 700;
	color: whitesmoke;
	text-align: center;
	padding: 0;
	margin: 0;
}

.sky {
	color: whitesmoke;
	font-weight: 600;
	letter-spacing: 1px;
	margin: 0;
	font-size: 27px;
	text-transform: capitalize;
}

.wind {
	display: flex;
	max-width: 220px;
	width: 100%;
	justify-content: space-around;
	align-items: center;
	color: whitesmoke;
	font-size: 24px;
	text-transform: capitalize;
	font-weight: 500;
}

@media only screen and (min-width: 320px) and (max-width: 575px) {
	.error {
		margin-top: 120px;
		font-size: 85px;
	}

	.boxItems {
		padding: 100px 0;
		border: none;
	}
}
